<template>
	<div class="large-deposit-search-transaction">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="" placeholder="" v-model="account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
		<mt-cell class="mint-select" title="" is-link>
            <select class="mint-field-core" v-model="status">
                <option value="0"></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
                <option value="4"></option>
                <option value="5"></option>
            </select>
        </mt-cell>
        <mt-cell title="" is-link></mt-cell>
        <mt-cell title="" is-link></mt-cell>
		<div class="field-action">
			<mt-button @click.native="back" type="primary"></mt-button>
			<submit-button @click.native="submit" type="primary" :submitStatus="submitStatus" value=""></submit-button>
		</div>
	</div>
</template>

<script>

export default{
	data() {
		return {
			status: '0',
			account: '',
            submitStatus: false,
			selectorPaymentAccount: {
                title: '',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '',
                        account: '625228***0089',
                        type: '',
                        isDefault: true,
                        idCardType: '',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '2',
                        account: '625228***0025',
                        type: ''
                    },
                    {
                        id: 3,
                        name: '3',
                        alias: '',
                        account: '625228***0109',
                        type: '',
                        bankName: ''
                    }
                ]
            }
		};
	},
    watch: {
        account: function(val) {
            if(val){
                this.submitStatus = true;
            }else{
                this.submitStatus = false;
            }
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push(`/large-deposit/search/tsearch-detail/${this.account}`);
        },
        back() {
        	history.back();
        }
	},
	created() {
		this.account = this.account || this.$store.state.largeDepositReplace.account;
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateLargeDepositReplace', {key: 'account', value: this.account});
        }else{
            this.$store.dispatch('updateLargeDepositReplace', {key: 'account', value: ''});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.large-deposit-search-transaction{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


