<template>
	<div class="large-deposit-buy">
        <mt-cell @click.native="showSelectorPaymentAccount()" title="" placeholder="" v-model="detail.account" is-link>
        </mt-cell>
		<selector-payment-account ref="selectorPaymentAccount" :options="selectorPaymentAccount" ></selector-payment-account>
        <mt-cell class="mint-select" title="" is-link>
            <select class="mint-field-core" v-model="detail.status">
                <option value=""></option>
                <option value="1"></option>
                <option value="2"></option>
                <option value="3"></option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="" is-link>
            <select class="mint-field-core" v-model="detail.date">
                <option value=""></option>
                <option value="1">1</option>
                <option value="3">3</option>
                <option value="6">6</option>
                <option value="9">9</option>
                <option value="12">12</option>
                <option value="18">18</option>
                <option value="24">24</option>
                <option value="36">36</option>
                <option value="60">60</option>
            </select>
        </mt-cell>
        <mt-cell class="mint-select" title="" is-link>
            <select class="mint-field-core" v-model="detail.type">
                <option value=""></option>
                <option value="1"></option>
            </select>
        </mt-cell>
		<div class="field-action">
            <mt-button @click.native="back" type="primary"></mt-button>
            <submit-button @submit="submit" :submitStatus="submitStatus" value=""></submit-button>
		</div>
	</div>
</template>

<script>

import { MessageBox } from 'mint-ui-rem-dpr';
import service from '@/services/bank/finance-products';

export default{
	data() {
		return {
			detail: {
                account: '',
				status: '2',
				date: '',
                type: '1'
			},
            submitStatus: false,
            seletsItem: {
                days: [
                    {
                        val: '',
                        desc: ''
                    }
                ],
                keeps: [
                    {
                        val: '',
                        desc: ''
                    }
                ],
                profits: [
                    {
                        val: '',
                        desc: ''
                    }
                ],
                status: [
                	{
                        val: '',
                        desc: ''
                    }
                ]
            },
			selectorPaymentAccount: {
                title: '',
                checked: {},
                showAddBtn: false,
                showWarn: false,
                accountList: [
                    {
                        id: 1,
                        name: '',
                        account: '625228***0089',
                        type: '',
                        isDefault: true,
                        idCardType: '',
                        idCard: '550505****50505'
                    },
                    {
                        id: 2,
                        name: '2',
                        account: '625228***0025',
                        type: ''
                    },
                    {
                        id: 3,
                        name: '3',
                        alias: '',
                        account: '625228***0109',
                        type: '',
                        bankName: ''
                    }
                ]
            }
		};
	},
    watch: {
        'detail.account': function(val) {
            if(val) this.submitStatus = true;
            else this.submitStatus = false;
        }
    },
	methods: {
		showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(data => {
                this.selectorPaymentAccount.checked = data.item;
                this.detail.account = data.item.account;
            }, data => {
            });
        },
        submit() {
        	this.$router.push(`/large-deposit/buy/list/${this.detail.account}`);
        },
        back() {
        	history.back();
        }
    },
	created() {
		// this.detail = Object.assign({}, this.$store.state.productApply.detail);
	},
	beforeRouteLeave (to, from, next) {
        if(to.meta['groupName'] === from.meta['groupName']){
            this.$store.dispatch('updateApplyOrder', {key: 'detail', value: this.detail});
        }else{
            this.$store.dispatch('updateApplyOrder', {key: 'detail', value: {account: '', code: '', name: '', day: '', keep: '', profit: '', status: ''}});
        }
        next( true );
    }
};

</script>

<style lang="scss">
.large-deposit-buy{
	.mint-cell-title,{
		width: 3.6rem;
	}
    .mint-cell.mint-select{
        .mint-cell-title{
            width: 3.6rem;
        }
    }
	.colorRed{
		.mint-field-core{
			color: red;
		}
	}
}
</style>


